
.validation.valid {
    border-color: green;
    color: green;
}
.validation.invalid {
    border-color: #990000;
    color: #990000;
}
.validation {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    color: #999;
    border: thin solid #999;
    padding: 2px 4px;
    border-radius: 4px;
    font-size: 1em;
}
.validation .btn {
    border-radius: 4px;
}
.header-day {
    background-color: #5c9ccc;
}
.header-day th {
    text-align: center;
    text-shadow: -1px 1px 0 rgba(255,255,255,.5);
    border-left: thin solid rgba(255,255,255,.3);
}
.header-day th small {
    font-weight: 100;
    display: block;
}
.table tbody tr:hover {
    background-color: #deebef;
}
.day.empty {
    font-weight: 100;
    color: rgba(0,0,0,.333);
}
.header-day .day.closed {
    background-color: #8f97a0 !important;
}
.interligne {
}
.interligne th {
    font-weight: 100;
    border-top: dotted thin #777;
    padding-top: 1em;
}
.subgroup {
    font-size: .8em;
}
.table tbody tr td {
    text-align: right;
    border-left: solid thin #ddd;
}
.soustotal { font-weight: 700;
}
.total { font-weight: 900;
}




